@font-face {
    font-family: 'Bahiana';
    src: url('img/Bahiana-Regular.woff2') format('woff2'),
        url('img/Bahiana-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

body {
  font-family: Bahiana;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.howto {
  margin-top:50px;
}

.next {
  position: absolute;
  right: 80px;
  top:80px;
}

.next_bt {
  background-image: url( "img/next.png");
}

.prev {
  position: absolute;
  left: 80px;
  top: 80px;
}

